<template>
  <div class="header">
    <img :src="backImgUrl" />
    <div class="header-divider">
      <div class="header-divider-inner"></div>
    </div>
    <div class="title">{{ $t('header.title').slice(0,4) }}</div>
    <div class="buttons">
      <PopWindowBut class="wechat"></PopWindowBut>
      <div class="language-switch">
        <el-dropdown @command="handleCommand">
          <img class="img" src="@/assets/header/china.png" v-if="'zh' === $i18n.locale">
          <img class="img" src="@/assets/header/japanese.png" v-else-if="'japanese' === $i18n.locale">
          <img class="img" src="@/assets/header/usa.png" v-else>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="japanese">日本語</el-dropdown-item>
            <el-dropdown-item command="zh">中文</el-dropdown-item>
            <el-dropdown-item command="en">English</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>

<script>
import PopWindowBut from './PopWindowBut.vue'
import backImg from '@/assets/header/background.jpg';
import testBack from '@/assets/header/testBackground.jpg'
export default {
  name: 'Header',
  components: { PopWindowBut },
  data() {
    return {
      backImg,
      testBack,
      backImgUrl: ''
    }
  },
  mounted() {
    this.backImgUrl = backImg
  },
  watch: {
    '$route'(val) {
      if(val.path === '/home') {
        this.backImgUrl = backImg
      } else {
        this.backImgUrl = testBack
      }
    }
  },
  methods: {
    handleCommand(lang) {
      this.$i18n.locale = lang // 设置给本地的i18n插件
      this.$message.success('success')
    }
  }
}
</script>

