<template>
  <div 
    class="nav" 
    :class="{
      'fixed-position': navIsVisible
    }"
  >
    <div class="nav-list">
      <div 
        class="nav-list-item" 
        v-for="(item,index) in NavList" 
        :class="{
          'active-nav': activeItemIndex === index,
          'english-nav': isEnglish
        }" 
        :key="index" 
        @click="handleSwitchNav(item,index)"
      >
        {{ $t("menu")[index] }} 
      </div>
    </div>
    <!-- <div class="nav-title">{{ $t("menu")[activeItemIndex] }}</div> -->
  </div>
</template>

<script>
import { NavList } from './Navigator';
import { debounce } from '@/utils/utils.js'
export default {
  name: 'Nav',
  data() {
    return {
      NavList,
      activeItemIndex: 0,
      isEnglish: false,
      io: null,
      navIsVisible: false
    }
  },
  watch: {
    '$i18n.locale'(val) {
      val === 'en' ? (this.isEnglish = true) : (this.isEnglish = false)
    }
  },
  mounted() {
    window.addEventListener('scroll', debounce(this.handleScroll,200));
  },
  methods: {
    handleSwitchNav(item,index) {
      this.activeItemIndex = index
      this.$router.push({path: item.router})
      this.bus.$emit('navIndexChange',index)
    },
    handleScroll() {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      if(scrollTop > 100) {
        this.navIsVisible = true
      } else {
        this.navIsVisible = false
      }
    }
  },
  beforeDestroy() {
  }
}
</script>

